import React, { SVGAttributes } from "react";

import * as WunderkindPatterns from "./Patterns";

export type WunderkindPatternsName = keyof typeof WunderkindPatterns;

export interface WunderkindPatternProps {
	name: WunderkindPatternsName;
	backgroundColor?: string;
	patternColor?: string;
	viewBox?: SVGAttributes<SVGElement>["viewBox"];
}

const Pattern = ({
	name,
	backgroundColor,
	patternColor,
	viewBox,
}: WunderkindPatternProps) => {
	const PatternComponent = WunderkindPatterns[name];
	PatternComponent.displayName = `${name}-pattern`;
	return (
		<PatternComponent name={name} backgroundColor={backgroundColor} patternColor={patternColor} viewBox={viewBox} />
	);
};

Pattern.displayName = "Pattern";
export default Pattern;
